.dark {
  background-color: rgb(32, 31, 31);
  color: white;
  transition: all 0.5s ease-in-out;
}

.light {
  background-color: white;
  color: rgb(32, 31, 31, 31);
  transition: all 0.5s ease-in-out;
}

.App {
  text-align: center;
}

body.dark .theme-button {
  background-color: white;
  color: red;
  border: none;
  border-radius: 20px;
  padding: 5px;
  cursor: pointer;
  transition: all 0.3s ease;
}

body.light .theme-button {
  background-color: rgb(2, 70, 134);
  color: black;
  border: none;
  padding: 5px;
  border-radius: 20px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.theme-button:hover {
  opacity: 0.8;
}

.cards-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.card {
  width: 300px;
  height: 200px;
  margin: 20px;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0);
  transition: all 0.3s ease;
}
